

const SingleProject = ({projectImg,projectName,link,alt}) => {
    return (
        <>
            <div className="inner-project">
                <div className="inner-project-img">
                    <a href={link} target="_blank" rel="noreferrer">
                        <img src={projectImg} alt={alt} className="img-fluid skeleton"  />
                        <div className="layer"><p className="project-title">{projectName}</p></div>
                        
                    </a>
                </div>
            </div>
        </>
    )
}


export default SingleProject;
