// Main Page About Section 

import ProfilePhoto from '../images/profile-photo-cropped.jpg';

const BannerAbout = () => {
    return (
      
            <section className="section-padding about-me" id="about-me">
            <div className="container">
                <div className="row justify-content-center align-items-center">
                <div className="col-11 text-center">
                    <h2 className="section-header">About Me</h2>
                    <h3 className="section-description mb-3">who i am</h3>
                </div>
                <div className="col-md-6 mt-3">
                    <div className="img-wrapper text-center">
                        <img  src={ProfilePhoto}  alt="personal" className="img-fluid personal-photo" />
                    </div>
                </div>
                <div className="col-md-6 mt-3">
                    <div className="abt-me-text py-3">
                        <div className="heading-text"> I'm Pratik and I'm a <span className="typing-2">Developer</span><span className="typed-cursor">|</span></div>
                        <p className="text-body">Seeking a position which will help me enhance my technical skills
                        and apply them to develop new and innovative ideas that would be
                        beneficial to fulfil the predefined objectives of the organization
                        </p>
                        <p className="text-body">I love to learn new technologies and implementing new ideas into my website. Trying to learn new things and developing new websites.</p>
                    </div>
                </div>
                </div>
            </div>
            </section>
        
    )
}


export default BannerAbout;