// Main Page

import BannerImage from '../images/developer-banner.jpg';
import DownloadCV from '../components/DownloadCV';
import Form from '../components/Form';
import BannerAbout from '../components/BannerAbout';
import SingleSkill from './SingleSkill';


const Home = () => {

    
    return (
        <>
        <section className="section-padding home">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-7">
                                <div className="home-content">
                                    <div className="text-1">Hello, my name is</div>
                                    <div className="text-2">Pratik Keluskar</div>
                                    <div className="text-3">And I'm a <span className="typing">Developer</span><span className="typed-cursor">|</span></div>
                                    <a href="#cv" className="hire-btn">Hire me</a>
                                </div>
                            </div>
                            <div className="col-md-5">
                            <img src={BannerImage} alt="banner-img" className="img-fluid" />
                            </div>
                        </div>
                    </div>
    </section>


    <section>
    <BannerAbout />
    </section>
    <section>
        <SingleSkill />
    </section>
    <section>
        <DownloadCV />
    </section>
    <section>
        <Form />
    </section>
    </>
    )
}

export default Home;
