// Main Page Download Cv Section

import DownloadImage from "../images/Cv-img.png";
import Resume from "../images/Pratik Keluskar resume.pdf";

const DownloadCV = () => {
    return (
        
                    <section className="section-padding bglight-red" id="cv">
                                <div className="container">
                                    <div className="row justify-content-center align-items-center">
                                        <div className="col-md-6 text-center">
                                             <h2 className="download-title">Check out CV</h2>
                                             <a href={Resume} target="_blank" className="download-btn" rel="noreferrer">Download Now</a>
                                        </div>
                                        <div className="col-md-6">
                                            <img src={DownloadImage} alt="Cv-img" className="img-fluid" />
                                        </div>
                                    </div>
                                </div>
                    </section>
        
    )
}

export default DownloadCV;
