// Main Projects Page
import TaskManager from './Projects/TaskManager/TaskManager';
import AllProjects from './Projects/Single-Projects/AllProjects';
import Incrementer from './Projects/Incrementer/Incrementer';
import Pokemon from './Projects/PokemonApi/Pokemon';
import FilterList from './Projects/FilterList/FilterList';

const Projects = () => {
    return (
        <div>   
            <div>
                <h2 className="section-header text-center my-2">Projects</h2>
            </div>
            <div className="project-border">
                 <TaskManager />
            </div>

            <div className="project-border">
                 <Incrementer />
            </div>
            
            <div className="project-border">
                <Pokemon />
            </div>
            <div className="project-border">
                <FilterList />
            </div>

            <AllProjects />
        </div>
    )
}


export default Projects;