// Main Page -> Skills Section


import Html from '../images/LOGOS/HTML.png';
import Css from '../images/LOGOS/css.png';
import Javascp from '../images/LOGOS/javascript.png';
import Bootstrap from '../images/LOGOS/bootstrap.png';
import MySQL from '../images/LOGOS/MySQL.png';
import Wordpress from '../images/LOGOS/wordpress.png';
import Java from '../images/LOGOS/java.png';
import Php from '../images/LOGOS/php.png';
import React from '../images/LOGOS/react-logo.png';

const SingleSkill = () => {
    return (
        <section className="section-padding">
            <div className="container">
                <div className="row">
                <div className="col-12 text-center">
              
                    <h2 className="section-header">My Skills </h2>
                </div>
                    <div className="col-md-3 text-center">
                        <div className='single-skill'>
                            <img src={Html} height="100px" width="100px" alt="html" />   
                        </div>
                    </div>
                    <div className="col-md-3 text-center">
                            <div className='single-skill'>
                                    <img src={Css} height="100px" width="100px" alt="html" />   
                                </div>
                    </div>
                    <div className="col-md-3 text-center">
                    <div className='single-skill'>
                                    <img src={Javascp} height="100px" width="100px" alt="html" />   
                                </div>
                    </div>
                    <div className="col-md-3 text-center">
                    <div className='single-skill'>
                                    <img src={Bootstrap} height="100px" width="100px" alt="html" />   
                                </div>
                    </div>
                    <div className="col-md-3 text-center">
                    <div className='single-skill'>
                                    <img src={MySQL} height="100px" width="100px" alt="html" />   
                                </div>
                    </div>
                    <div className="col-md-3 text-center">
                    <div className='single-skill'>
                                    <img src={Wordpress} height="100px" width="100px" alt="html" />   
                                </div>
                    </div>
                    <div className="col-md-3 text-center">
                    <div className='single-skill'>
                                    <img src={Java} height="100px" width="100px" alt="html" />   
                                </div>
                    </div>
                    <div className="col-md-3 text-center">
                    <div className='single-skill'>
                                    <img src={Php} height="100px" width="100px" alt="html" />   
                                </div>
                    </div>
                    <div className="col-md-12 text-center">
                        <div className="row justify-content-center">
                            <div className="col-md-3 text-center">
                               <div className='single-skill react-logo'>
                                    <img src={React} height="100px" width="100px" alt="html" />   
                                </div>
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
        </section>

    )
}


export default SingleSkill;
