// Main Page -> Form Section

const Form = () => {
    return (
       
                        <section className="section-padding contact-me" id="contact-me">
                    <div className="container">
                        <div className="row">
                                <div className="col-md-6">
                                <div className="contact-text">
                                    <div className="section-header mb-3">Get in Touch</div>
                                    <p className="text-body">Hey there, Interested in talking here are my details If you are looking forward to talk...</p>
                        
                                            <div className="info mb-2">
                                                <div className="text-crimson font-weight-bold">Name</div>
                                                <div className="sub-title text-crimson font-weight-bold">Pratik Keluskar</div>
                                            </div>
                                        
                                            <div className="info mb-2">
                                                <div className="text-crimson font-weight-bold">Address</div>
                                                <div className="sub-title text-crimson font-weight-bold">Mulund East, Mumbai</div>
                                            </div>
                                            
                                            <div className="info mb-2">
                                                <div className="text-crimson font-weight-bold">Email</div>
                                                <div className="sub-title text-crimson font-weight-bold">pratikkeluskar1997@gmail.com</div>
                                            </div>
                                    
                                </div>
                                </div>

                                <div className="col-md-6">
                                <div className="section-header mb-3 message-header">Message me</div>
                                <form action="#">
                                
                                            <div className="fields">
                                            <div className="row">
                                                <div className="col-12 col-md-6">
                                                    <div className="field name">
                                                        <input type="text" placeholder="Name" required="" className="input-name"/>
                                                    </div>
                                                </div>
                                                <div className="col-12 col-md-6">
                                                    <div className="field email">
                                                        <input type="email" placeholder="Email" required="" className="input-email"/>
                                                    </div>
                                                    </div>
                                            </div>
                                            <div className="field">
                                            <div className="row mt-3">
                                                <div className="col-12"> 
                                                <input type="text" placeholder="Subject" required="" className="input-subject" />
                                                </div>
                                            </div>
                                                
                                            </div>

                                            <div className="field textarea">
                                            <div className="row mt-3">
                                                <div className="col-12"><textarea cols="30" rows="10" placeholder="Message.." required="" className="input-text-area"></textarea></div>
                                            </div>
                                                
                                            </div>
                                            <div className="button-area">
                                            <div className="row mt-3">
                                                <div className="col-12"><button type="submit" className="input-send-btn"> Send message</button></div>
                                            </div>
                                                
                                            </div>
                                    
                                
                                </div></form>
                        </div>
                    </div>
                    </div>
        </section>
        
    )
}

export default Form;
