// Main Navigation Bar

import { Link } from "react-router-dom";

const Nav = () => {
    return (
        <header>
      
        <nav className="navbar navbar-expand-md navbar-light">
                        <div className="container">
                                         <Link to="/pratikk"><span className="navbar-brand"><span className="logo">
                                         <span className="change-icon">
                                            Portfoli
                                            <i className="far fa-smile"></i>
                                            <i className="far fa-smile-wink"></i>
                                        </span></span></span> </Link>
                                        <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                        <span className="navbar-toggler-icon"></span>
                                        </button>                
                                        <div className="navbar-collapse collapse justify-content-end" id="navbarSupportedContent" >
                                            <ul className="navbar-nav ml-auto text-center text-md-left">
                                                <li className="nav-item active p-2">
                                                <Link to="/pratikk" >
                                                <span className="nav-link" >Home</span>
                                                </Link>
                                                </li>
                                                <li className="nav-item p-2">
                                                <Link to="/about">
                                                <span className="nav-link">About Me</span></Link>
                                                </li>
                                                
                                                <li className="nav-item p-2">
                                                <Link to="/projects">
                                                    <span className="nav-link">Projects</span></Link>
                                                </li>
                                               
                                            </ul>
                                        </div>
                            </div>
        </nav>
  
</header>
    )
}

export default Nav;
