import { useState } from "react";
import "../TaskManager/taskmanager.css";
const Incrementer = () => {

    const [number,setNumber] = useState(0);
    return (
        <div className="section-padding">
            <section className="text-center">
            <h2 className="blue-color">Counter</h2>
            </section>
            <section className=" text-center">
                <button className="counter-btns" onClick={()=> setNumber(number + 1)}>Increment </button>
                <span className="inc-val">{number}</span>
                <button className="counter-btns" onClick={()=> setNumber(number - 1)}> Decrement </button>
            </section>
            <section className="text-center">
            <button className="counter-btns reset-btn" onClick={()=> setNumber(0)}> Reset </button>
            </section>

        </div>
    )
}


export default Incrementer;