
const PokemonList = ({pokemon}) => {
  
    return (
        <div className="container py-5">
        <div className="row">
            <div className="col-12 text-center"><h2 className="blue-color">Pokemon Api</h2></div>
            <div className="col-12 list-poke">
            {pokemon.map(pokemon =>(
           <h6 key={pokemon}> {pokemon}</h6>
            ))}
        </div></div>
        </div>
    )
}

export default PokemonList;
