import JsonData from './MOCK_DATA.json';
import { useState } from 'react';

const FilterList = () => {

    const[searchTerm,setSearchTerm] = useState('')

    return (
        <div className="section-padding list-sections">
        <div className="text-center"><h2 className="blue-color"> Filter List</h2></div>
            <input type="text" onChange={(event) => { setSearchTerm(event.target.value);}} className="search-filter" placeholder="Search..." />
            <div className="list-ren">
            {JsonData.filter((val) => { 
                if(searchTerm === ""){
                    return val
                }
                else if (val.first_name.toLowerCase().includes(searchTerm.toLowerCase())){
                    return val
                }
            }).map((val,key) =>{
                return <p key={key}>{val.first_name}</p>
            })}
        </div>
        </div>
    )
}


export default FilterList;